  public async exportAsExcelFile( ) {

    const Loader = this.loadingCtrl.create({
      message:"Exporting Data Please Wait...",
      animated:true,
    });

    (await Loader).present();

    let json:any[]=[]



    this.filteredTableData.forEach((rowData:object) =>{
      let singleRow :object ={}
      this.tableHeader.forEach((singleHeader:any) =>{
          
        singleRow = {...singleRow,[singleHeader.columnName]:rowData[singleHeader.columnName]};
      })
      json.push(singleRow)
    })


    //let excelFileName: string="myexcel"
    const fileName:string = this.reportName + "_"+ this.datePipe.transform(new Date(), 'yyyy-MM-dd') +".xlsx";

 

    const workbook: XLSX.WorkBook = { Sheets: {}, SheetNames: [] };

    // Add rows with data at the top
    const customData = [
     
    ];


    if(this.getFarmName()){
     let reportHeader=[]
     reportHeader.push(this.getFarmName())
    customData.push(reportHeader)
    }
    
    if(this.getCHCname()){
      let reportHeader=[]
     reportHeader.push(this.getCHCname())
    customData.push(reportHeader)
    }
  
     if(this.getCCname()){
      let reportHeader=[]
      reportHeader.push(this.getCCname())
     customData.push(reportHeader)
      }
  
      if(this.reportName){
        let reportHeader=[]
        reportHeader.push(this.reportName)
       customData.push(reportHeader)
      }
     




  
    const worksheet: XLSX.WorkSheet = XLSX.utils.aoa_to_sheet(customData);
  
    // Add the custom rows at the top
    for (let rowNum = 0; rowNum < customData.length; rowNum++) {
      const rowData = customData[rowNum];
      for (let colNum = 0; colNum < rowData.length; colNum++) {
        const cell = XLSX.utils.encode_cell({ r: rowNum, c: colNum });
        worksheet[cell] = { v: rowData[colNum], t: 's' };
      }
    }
  
    // Append the existing data to the worksheet
    let rowOffset = customData.length+1;
    for (const rowData of json) {
      const row = [];
      for (const cellData of Object.values(rowData)) {
        row.push(cellData);
      }
      XLSX.utils.sheet_add_aoa(worksheet, [row], { origin: rowOffset });
      rowOffset++;
    }
  
    // Extract the keys from the first row of data
    const keys = Object.keys(json[0]);
  
    // Add the key names as a separate row after the custom data rows
    const keyRowOffset = customData.length;
    const keyRow = keys.map(key => key.toUpperCase());
    XLSX.utils.sheet_add_aoa(worksheet, [keyRow], { origin: keyRowOffset });
  
    // Assign the worksheet to the 'data' sheet in the workbook
    workbook.SheetNames.push('data');
    workbook.Sheets['data'] = worksheet;



    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'buffer' });
     await this.saveAsExcelFile(excelBuffer, fileName);

     (await Loader).dismiss();
  }

  private saveAsExcelFile(buffer: any, fileName: string): void {
    const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
    const data: Blob = new Blob([buffer], {
      type: EXCEL_TYPE
    });
    saveAs(data, fileName);

    
  }


 
 


