  multiSelectChange(event: {
    component: IonicSelectableComponent,
    value: any
  },id,selectedValues:any[]) {


    if (screen.width <= 480) {
      id=id+'-mobile';
    }

    let valueToApply :any ='';

    if(selectedValues.length!=0){
      selectedValues.forEach(element => {
        valueToApply=element.value + ',' + valueToApply
      });
      valueToApply = valueToApply.substring(0, valueToApply.length - 1);
    }
    else{
      valueToApply=''
    }

    
    
    (document.getElementById(id) as HTMLInputElement).value = valueToApply;
  }

  singleSelectChange(event: {
    component: IonicSelectableComponent,
    value: any
  },id,selectedValue:any) {

    if (screen.width <= 480) {
      id=id+'-mobile';
    }

    let valueToApply:any;
    if(selectedValue!=null || selectedValue!=undefined){
         valueToApply=selectedValue.value;
    }
    else{
      valueToApply=''
    }
    (document.getElementById(id) as HTMLInputElement).value = valueToApply;
    
    
    
  }

  
  
  

