  async plotfilters() {
    let loading = this.loadingCtrl.create({
      message:'Please Wait....'
    });
    (await loading).present();
    let userId = this.getFarmerUserID();
    let farmId = this.getFarmID();
    let chcId = this.getCHCUserID();
    let ccId = this.getCCUserID();
    let farmerId = this.getFarmerID();
    let userType = this.getUserType();
    let organization = "0";
    this.dataService
      .plotfilters(
        this.key,
        this.reportId,
        this.DatabaseCode,
        userId,
        farmId,
        chcId,
        ccId,
        farmerId,
        userType,
        organization
      )
      .subscribe(async (res) => {
        //console.log(res,"Filters")
        this.filters = res;
        res.forEach((element) => {

          this.totalFilters = this.totalFilters + "," + element.filterName;
        });
        this.buildUserFilter();
        await (await loading).dismiss();

        // const dateControl = document.getElementById('Daily Date') as HTMLInputElement;
        const date = document.querySelectorAll('input[type="date"]');
        let dateArr = Array.from(date);
        dateArr.forEach((x) => {
          let temp = <HTMLInputElement>x;
          temp.defaultValue = this.datePipe.transform(new Date(), "yyyy-MM-dd");
          
        });

        //yyy this.datePipe.transform( new Date().toLocaleDateString());
      });

  }

