  getMultiSelect(optionsList: any, selectElement: any) {
    
    let tempOptions = document.getElementById(optionsList).children;
    let options = Array.from(tempOptions);

    options = options.filter(
      (x) =>
        x.className ==
        "item item-block item-md item-checkbox item-checkbox-checked"
    );
    let newArr: any[] = [];
    let dispToAppend :any[]=[]
    options.forEach((x) => {
      let tem = <HTMLInputElement>x.firstElementChild;
      newArr.push(tem.value);
      dispToAppend.push((x.children[1] as HTMLInputElement).innerText)
    });
    if (newArr.length) {
    
      this.dataselection(newArr, selectElement,dispToAppend);
    }

   
    let srcbar = document.getElementById(selectElement+'-search') ;
          
          let clearbtn =srcbar.children[0].children[3] as HTMLButtonElement
            clearbtn.click()
  }


