  removeduplicate(finaldata) {
    const ids = finaldata.map((o) => o.SerialNo);
    var tmpDataToPass = finaldata.filter(
      ({ SerialNo }, index) => !ids.includes(SerialNo, index + 1)
    );
    return tmpDataToPass;
  }
